const marginS = '15px';
const imageSizeM = '270px';
const marginL = ' 75px';
const marginXL = '200px';
const button = {
	backgroundColor: 'rgba(100,100,100,0.35)',
	padding: '0.5rem 1rem',
  fontWeight:"300",
	margin: '0.5rem auto 0 auto',
	color: 'white',
};

const collections_block = {
	backgroundRepeat: 'no-repeat',
	backgroundSize: 'cover',
	display: 'flex',
	justifyContent: 'center',
	flexDirection: 'column',
	backgroundPosition: 'center',
	minHeight: '40vh',
	maxWidth: '1024px',
	padding: marginXL + marginL,
	margin: marginS + ' auto',
};
const collections_block__text = {
	margin: '0 auto',
	color: 'white',
	fontSize: '1.4rem',
	textAlign: 'center',
	fontWeight: 'bold',
};
const collections_block__image = {
	maxHeight: imageSizeM,
};

const whoAmI = {
	...collections_block,
	...{
		backgroundImage: 'linear-gradient(rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3)), url(https://sun9-70.userapi.com/c857136/v857136478/13cc76/EQ9poSMtFok.jpg)',
	},
};
const wooden_cupholders = {
	...collections_block,
	...{
		backgroundImage: 'linear-gradient(rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3)),url(https://sun9-34.userapi.com/c857428/v857428960/1b6675/BASkOvJrKe4.jpg)',
	},
};
const master_class1 = {
	...collections_block,
	...{
		backgroundImage: 'linear-gradient(rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3)),url(https://sun9-70.userapi.com/c857036/v857036960/d38c9/ZAbUvYg9hvE.jpg)',
	},
};
const master_class2 = {
	...collections_block,
	...{
		backgroundImage: 'linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.3)),url(https://sun9-34.userapi.com/c857428/v857428960/1b6675/BASkOvJrKe4.jpg)',
	},
};
const collections = {
	maxWidth:'500px',
	margin:'0 auto'
};
const CollectionsComponent = props => (
	<div style={collections}>
		<div style={whoAmI}>
			<a style={collections_block__text} href={'/whoami'}>ОБО МНЕ</a>
			<a style={button} href={'/whoami'}>
				Посмотреть
			</a>
		</div>
		<div style={master_class1}>
			<a style={collections_block__text} href={'/study1'}>ГАЙД ПО СОЗДАНИЮ СРЕЗОВ КАМНЕЙ (+видео)</a>
			<a style={button} href={'/study1'}>
				Посмотреть
			</a>
		</div>
		<div style={master_class2}>
			<a style={collections_block__text} href={'/study2'}>ВИДЕО-КУРС ПО ВИТРАЖНЫМ ПОДСТАКАННИКАМ</a>
			<a style={button} href={'/study2'}>
				Посмотреть
			</a>
		</div>
	</div>
);
export default CollectionsComponent;
